/*******************************BLANCO*****************************/
#cntr_home {
    margin-top: 10%;
}

#home {
    display: none;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 0;
}

li {
    display: none;
}

li a {
    display: block;
    color: black;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-weight: bold;
}

#cuadrado {
    background-color: #D9D9D9;
    border-radius: 15px;
    height: 150px;
    width: 150px;
    margin-bottom: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 10px;
}

#iconos {
    height: 110px;
}